html, body {
  width: 100%;
  height: 100%;
  margin:  0;
  padding: 0;
  background-color: #fff;
}

.wrapper {
  position: relative;
  height: 100%;
}

.container {
  padding: 20px 0 15px;
  width:  520px;
  height: auto;
  min-height: 350px;
  background-color: #fff;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  /* Border Radius */
  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border: 0px solid #000000;

  /* Drop Shadow */
  -webkit-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.34);
  -moz-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.34);
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.34);
}


.logo__brand-logo {
  margin: 0 auto 15px;
  width:  205px;
  height: auto;
}
.logo__brand-logo img {
  width:  100%;
  height: auto;
}

/* INPUT FIELDS */
textarea, select,
input, button {
  outline: none;
}

input:focus::-webkit-input-placeholder {
  color: transparent;
}

.input__container {
  margin: 0 auto 0;
  width:  300px;
  height: auto;
}

.input {
  border-size: border-box;
  margin: 0 0 6px;
  padding: 0 0 0 10px;
  width:  300px;
  height: 35px;
  font-size: 12px;

  /* Border Radius */
  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border: 1px solid #E2E2E2;
}

.input--error {
  border: 1px solid red;
}

.input__label {
  padding-bottom: 10px;
  color: #6c6c6c;
  font-weight: 600;
}

.input__error-message {
  width: 100%;
  height: 25px;
  font-size: 14px;
  color: red;
}

/* PASSWORD HELPER */
.password-helper {
  box-sizing: border-box;
  margin: -10px 0 5px 0;
  padding: 10px 10px;
  width: 312px;
  height: auto;
  font-size: 12px;
  background-color: #E2E2E2;

  /* Border Radius */
  border-radius: 0 0 4px 4px;
  -moz-border-radius: 0 0 4px 4px;
  -webkit-border-radius: 0 0 4px 4px;
}

.toggle-password__wrapper {
  position: relative;
}

.toggle-password {
  position: absolute;
  top:   10px;
  right: 0;
  font-size: 14px;
  color: #4A90E2;
  text-align: right;
  cursor: pointer;
}

.spacer {
  width: 100%;
  height: 20px;
}

/* LINK */
.link {
  margin: 0 0 30px;
  width:100%;
  text-align: center;
}
.link a {
  color: #4A90E2;
  text-decoration: none;
}

/* BUTTONS */
.btn {
  display: inline-block;
  box-sizing: border-box;
  margin: 5px auto 15px;
  width:  312px;
  height: 50px;
  font-size: 16px;
  font-weight: 600;
  text-align: center;

  /* Border Radius */
  border-radius: 4px;
  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border: 0px solid #000000;
}

.btn__wrapper {
  width: 100%;
  text-align: center;
}

.btn__primary {
  color: #fff;
  background-color: #364763;
}
.btn__inactive {
  color: #fff;
  background-color: #aeaeae;
}

.social-login-wrapper {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 0 auto;
  width: 300px;
}

.btn__social-login {
  display: inline-block;
  padding: 16px 0 0;
  width: 145px;
}

.btn__social-login--facebook {
  color: #fff;
  background-color: #3B5998;
}

.btn__social-login--google {
  color: #fff;
  background-color: #CE543B;
}

.btn__link {
  font-size: 16px;
  font-weight: 600;
}

.btn__link--primary {
  color: #fff;
  text-decoration: none;
}

.btn__link--inactive {
  cursor: not-allowed;
  color: #fff;
  text-decoration: none;
}

/* FOOTER */

.footer {
  position: absolute;
  bottom: 18px;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  margin: 30px 0 0;
  width:  100%;
}
.footer p {
  margin: 2px 0 0;
  height: 10px;
  font-size: 10px;
}
.footer img {
  width:  80px;
  height: 100%;
  margin: 0 8px 0 0;
}
